.search-icon {
  position: relative;
  width: 24px;
  height: 25px;
}
.article-name-or {
  position: relative;
  line-height: 26px;
  display: inline-block;
  width: 560px;
  flex-shrink: 0;
}
.search-box {
  border-radius: var(--br-xs);
  background-color: var(--input-field-background);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.all-categories {
  position: relative;
  line-height: 26px;
  display: inline-block;
  width: 228px;
  flex-shrink: 0;
}
.caret-icon {
  position: relative;
  width: 24.83px;
  height: 9.05px;
}
.drop-down {
  border-radius: var(--br-xs);
  border: 2px solid var(--input-field-background);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-5xl);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  cursor: pointer;
}
.search1 {
  position: relative;
  line-height: 26px;
}
.button {
  border-radius: var(--br-xs);
  background-color: var(--black);
  flex-direction: row;
  padding: var(--padding-5xl);
  color: var(--white);
}
.button,
.search-components,
.search-module {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.search-components {
  flex-direction: row;
  gap: var(--gap-base);
}
.search-module {
  position: absolute;
  top: 72px;
  left: calc(50% - 564.5px);
  border-radius: var(--br-xl);
  background-color: var(--white);
  flex-direction: column;
  padding: var(--padding-5xl);
}
.search {
  position: relative;
  background-color: var(--page-background);
  width: 100%;
  height: 800px;
  overflow: hidden;
  text-align: left;
  font-size: var(--regular-text-size);
  color: var(--black);
  font-family: var(--regular-text);
}
